import "../style/home-page/page.scss";
function Apparatus() {
  return (
    <div className="page">
      <div className="home-page-theme">
        <h1>Aparatura</h1>
        <p>
          Laboratorium posiada nowoczesn aparatur badawcz o wysokiej czuoci
          i szerokim spektrum pomiarowym. Aparatura pomiarowa wchodzca w skad
          naszego laboratorium umoliwia prowadzenie bada strukturalnych,
          optycznych i fotoelektrycznych. Jestemy w stanie scharakteryzowa
          materiay lite, ciecze, a nawet gazy. Dziki komplementarnie dobranym
          badaniom moemy zidentyfikowa m.in. struktur czsteczkow
          materiaw, przemiany fazowe, kinetyk reakcji chemicznych i
          efektywno w warunkach pracy. Nasze urzadzenia potrafi w peni
          scharakteryzowa badany materia, dziki czemu moemy dostarczy
          precyzyjnej odpowiedzi dla biznesu i przemysu. Dugoletnie
          dowiadczenie oraz gboka wiedza akademicka, umoliwia nam
          konstruowa precyzyjne ukady optyczne, stanowiska badawcze, a nawet
          unikalne stanowiska pomiarowe umoliwiajce innowacyjne badania. Nasz
          interdyscyplinarny zesp gwarantuje znalezienie optymalnych rozwiza
          i opracowa odpowiednie narzdzia.
        </p>
        <p>
          Najwaniejsze potrafimy wytworzy przeroczyst ceramik o wysokiej
          staej Verdeta do zastosowania w izolatorach optycznych dla laserw
          duej mocy i cyrkulatorach telekomunikacyjnych.
        </p>{" "}
        <br></br>
      </div>

      <div className="home-page-theme">
        {/* <p className="red">Wykaz urzdze oraz aparatury pomiarowej:</p> */}
        <h1>Wykaz urzdze oraz aparatury pomiarowej:</h1>
        <div className="devices-list">
          <ol>
            <li>
              Spektrometr FT-IR firmy Shimadzu (zakres 400 cm-1  4000 cm-1),
              wspczynnik sygnau do szumu (S/N)  60 000:1, rozdzielczo 
              0.25 cm-1, prdkoci skanowania  do 20 widm na sekund.
              Wyposaony w detektory rozszerzajce zakres pomiarowy w FIR i NIR
              czyli od 240 cm-1 do 12400 cm-1. Przystawka odbiciowa GladiATR
              firmy PIKE z kamer umoliwia pomiar materiaw litych, proszkw i
              cieczy.
            </li>
            <li>
              Spektrofotometr fotoelektryczny firmy Instytut Fotonowy 
              umoliwia pomiar przewodnictwa prdu elektrycznego materiaw
              optycznie aktywnych o duej przerwie energetycznej w funkcji
              przyoonego napicia i dugoci padajcej fali. Materia mona
              owietla wiatem biaym lub wybran fal o dugoci od 200 nm do
              1100 nm ze skokiem 1 nm. Aparatura suy m in. do testowania
              materiaw optycznie czynnych, fotodiod i paneli sonecznych.
            </li>
            <li>
              Spektrometr SilverNova StellarNet pracujcy w zakresie od 190
              nm-1110 nm z rozdzielczoci 0.5 nm.
            </li>
            <li>
              Spektrometr NIRQUEST+2.5 OceanInsight umoliwiajcy pomiary w
              zakresie podczerwieni od 900 nm do 2500 nm.
            </li>
            <li>
              Spektrometr wysokorozdzielczy Mechelle na zakres VIS z kamer CCD
              firmy Oxford Insruments typu Echelle do precyzyjnych bada
              optycznych.
            </li>
            <li>
              Urzdzenia sprzgnite s szerokopasmowymi wiatowodami ze sfer
              cakujc IC2 firmy StellarNet. <br></br> Dziki tym urzdzeniom,
              moliwe s pomiary m in. transparencji, absorbcji, barwy,
              starzenia materiau, wyznaczania przerwy energetycznej, badania
              zjawisk fotochromowych i luminescencjnych.
            </li>
            <li>
              Aparatura pomiarowa firmy Shimadzu SALD-7500nano do analizy
              rozkadu wielkoci ziaren dziaajca w oparciu o technik DLS
              (dynamicznego rozpraszanie wiata). Urzdzenie wykorzystuje laser
              o dugoci fali 405 nm, ktry umoliwia badanie czstek od 0.7 nm
              do kilku mikrometrw.
            </li>
            <li>
              Oscyloskop cyfrowy czterokanaowy Rhode-Schwarz 4 GHz umoliwia
              pomiary szybkich zjawisk elektrycznych i optycznych.
            </li>
            <li>
              Szybkie fotodiody krzemowe na zakres widzialny oraz fotodiody typu
              InAgAs czue na zakres podczerwieni. Umoliwiaj pomiary czasu
              zaniku luminescencji.
            </li>
            <li>
              Laser Ekspla typu DSPP oraz OPO, emitujcy dowoln dugo fali w
              zakresie od 210 do 2500nm z repetycj 10 Hz lub 20 Hz. Umoliwia
              badania luminescencyjne oraz wykonywanie do ablacji laserowej.
            </li>
            <li>
              Lasery pracy cigej 405 nm oraz 808 nm firmy Spectra-laser
              umoliwiajce badania fizykochemiczne dowolnych materiaw.
            </li>
            <li>
              Lampy halogenowo-deuterowe emitujce stabilne wiato o widmie
              cigym w zakresie 280-2500 nm.
            </li>
            <li>
              Unikalne stanowisko do pomiarw efektu Faradaya oparte o laser
              Ekspla, system pola magnetycznego generowanego przez cewki w
              ukadzie Helmholtza oraz zbalansowany polarymetr. W celu
              poprawienia detekcji sygnau, wykorzystuje si precyzyjny
              wzmacniacz fazoczuy typu Lock-in firmy SRS. Stanowisko umoliwia
              znalezienie staej Verdeta dla materiaw przeroczystych oraz
              wspczynnika absorbcji , transparencji w danym punkcie materiau.
            </li>
            <li>
              Kamera EIS czyli Czasowo-rozdzielczy Elektrochemiczny Spektroskop
              Impedancyjny (TR- EIS) firmy Instytut Fotonowy umoliwiajcy
              badania waciwoci elektrycznych i mikrostrukturalnych oraz
              elektrochemicznych materiaw. Dziaa w zakresie od milihercw do
              1MHz. Urzdzenie sprzone jest ze stanowiskiem pomiarowym
              dziaajcym do 1000K w atmosferze powietrza lub gazie obojtnym.
              Urzdzenie umoliwia ledzenia procesw korozji, wyadowania
              baterii, testy ogniw wodorowych, hydratacji materiaw wicych,
              w tym cementw, betonw i wielu innych.
            </li>
            <li>
              Uzupenieniem stanowiska jest precyzyjny przyrzd pomiarowy mostek
              RLC firmy RhodeSchwartz umoliwiajcy pomiary przewodnictwa prdu
              przemiennego do 10MHz w materiaach dielektrycznych i
              pprzewodnikach.
            </li>
            <li>
              Stanowisko do pomiarw ruchliwoci nonikw elektrycznych
              (elektrony, dziury) metod Halla oparte o nanowoltomierz oraz
              precyzyjny generator napicia.
            </li>
          </ol>
        </div>
      </div>
    </div>
  );
}

export default Apparatus;
